@-webkit-keyframes yourAnimation {
  0% {
    -webkit-transform: rotate(0) translateX(-30px);
            transform: rotate(0) translateX(-30px);
    opacity: 0;
  }
  70% {
    -webkit-transform: rotate(0) translateX(15px);
            transform: rotate(0) translateX(15px);
    opacity: 0.76;
  }
  100% {
    -webkit-transform: rotate(0) translateX(0px);
            transform: rotate(0) translateX(0px);
    opacity: 1;
  }
}

@keyframes yourAnimation {
  0% {
    -webkit-transform: rotate(0) translateX(-30px);
            transform: rotate(0) translateX(-30px);
    opacity: 0;
  }
  70% {
    -webkit-transform: rotate(0) translateX(15px);
            transform: rotate(0) translateX(15px);
    opacity: 0.76;
  }
  100% {
    -webkit-transform: rotate(0) translateX(0px);
            transform: rotate(0) translateX(0px);
    opacity: 1;
  }
}

.title {
  -webkit-animation: yourAnimation 0.7s forwards 0.3s ease;
          animation: yourAnimation 0.7s forwards 0.3s ease;
}

@-webkit-keyframes yourAnimation2 {
  0% {
    -webkit-transform: rotate(0) scale(0.8) skewY(25deg);
            transform: rotate(0) scale(0.8) skewY(25deg);
    opacity: 0;
  }
  100% {
    -webkit-transform: rotate(0) scale(1) skewY(0deg);
            transform: rotate(0) scale(1) skewY(0deg);
    opacity: 1;
  }
}

@keyframes yourAnimation2 {
  0% {
    -webkit-transform: rotate(0) scale(0.8) skewY(25deg);
            transform: rotate(0) scale(0.8) skewY(25deg);
